import React from "react";
import { Link } from "react-router-dom";
import "./TrangChu.css";

const TrangChu = () => {
  const stories = [
    {
      title: "V luyn nh phong",
      image:
        "https://cdn.truyenfull.tv/medias/covers/5/5842-vu-luyen-dien-phong-vo-luyen-dinh-phong_cover_large.jpg",
      description:
        "Lot truyn c th ni l ni ting nht trong th loi tin hip.",
      chapters: [
        { title: "Chng 1", href: "/voluyendinhphong" },
        { title: "Chng 2", href: "/voluyendinhphong2" },
      ],
    },
    {
      title: "Ngn tay b n",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSXJnuIVxHqHp7a-QE4zoyukCvY1L5zJjALmA&s",
      description: "Cu truyn creepypasta kh ni ting.",
      chapters: [{ title: "Chng 1", href: "/ngontaybian" }],
    },
    {
      title: "Harry Potter v Hn  Ph Thy",
      image:
        "https://cdn.truyenfull.tv/medias/covers/38/38517-harry-potter-va-hon-da-phu-thuy-quyen-1_cover_large.jpg",
      description:
        "Phn u tin ca lot truyn ni ting v cu b ph thy.",
      chapters: [
        { title: "Chng 1", href: "/harrypotter/chapter1" },
        { title: "Chng 2", href: "/harrypotter/chapter2" },
        { title: "Chng 3", href: "/harrypotter/chapter3" },
      ],
    },
    {
      title: "It",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQ4IAcJFFfj5o7zLivJv7Urt0T_twKNiphh1w&s",
      description: "G h kinh d.",
      chapters: [
        { title: "Chng 1", href: "/it/chapter1" },
        { title: "Chng 2", href: "/it/chapter2" },
      ],
    },
  ];

  return (
    <div className="story-list">
      <h1>Trang Ch Truyn</h1>
      <div className="story-overview">
        {stories.map((story, index) => (
          <div key={index} className="story">
            <h3 className="story-title">{story.title}</h3>
            <img src={story.image} alt={story.title} />
            <p>{story.description}</p>
 
            <div className="chapter-list">
              {story.chapters.map((chapter, i) => (
                <Link to={chapter.href} key={i}>
                  <button className="chapter-button">{chapter.title}</button>
                </Link>
              ))}
            </div>
          </div>
        ))}
      </div>
      <br />
      <br />
      <br />
      <div className="footer-bar">
        <p>Chc bn c truyn vui v!</p>
      </div>
    </div>
  );
};

export default TrangChu;
