import React from "react";
import { Link } from "react-router-dom";
import Navbar from "./navcuatrangtruyen.js";
const KinhDi = () => {
  return (
    <div className="Tin hip">
      <Navbar />
      <h1>Th Loi: phiu lu</h1>
      <div className="story-list">
        <div className="story">
          <h3>V Luyn nh Phong</h3>
          <p>
            Lot truyn c th ni l ni ting nht trong th loi tin hip.
          </p>
          <Link to="/harrypotter/chapter1">Chng 1</Link>
          <br />
          <Link to="/harrypotter/chapter2">Chng 2</Link>
          <br />
        </div>
      </div>
    </div>
  );
};

export default KinhDi;
